import uniStatusBar from '../uni-status-bar/uni-status-bar.vue'
import uniIcon from '../uni-icon/uni-icon.vue'

export default {
	components: {
		uniStatusBar,
		uniIcon
	},
	props: {
		/**
		 * 标题文字
		 */
		title: {
			type: String,
			default: ''
		},
		/**
		 * 左侧按钮文本
		 */
		leftText: {
			type: String,
			default: ''
		},
		/**
		 * 右侧按钮文本
		 */
		rightText: {
			type: String,
			default: ''
		},
		/**
		 * 左侧按钮图标
		 */
		leftIcon: {
			type: String,
			default: ''
		},
		/**
		 * 右侧按钮图标
		 */
		rightIcon: {
			type: String,
			default: ''
		},
		/**
		 * 是否固定在顶部
		 */
		fixed: {
			type: [Boolean, String],
			default: false
		},
		/**
		 * 按钮图标和文字颜色
		 */
		color: {
			type: String,
			default: '#000000'
		},
		/**
		 * 背景颜色
		 */
		backgroundColor: {
			type: String,
			default: '#4680FA'
		},
		/**
		 * 是否包含状态栏，默认固定在顶部时包含
		 */
		statusBar: {
			type: [Boolean, String],
			default: ''
		},
		/**
		 * 是否使用阴影，默认根据背景色判断
		 */
		shadow: {
			type: String,
			default: ''
		}
	},
	computed: {
		isFixed() {
			return String(this.fixed) === 'true'
		},
		insertStatusBar() {
			switch (String(this.statusBar)) {
				case 'true':
					return true
				case 'false':
					return false
				default:
					return this.isFixed
			}
		},
		hasShadow() {
			var backgroundColor = this.backgroundColor
			switch (String(this.shadow)) {
				case 'true':
					return true
				case 'false':
					return false
				default:
					return backgroundColor !== 'transparent' && backgroundColor.indexOf('rgba') < 0
			}
		}
	},
	methods: {
		/**
		 * 左侧按钮点击事件
		 */
		onClickLeft() {
			// this.$emit('clickLeft')
			this.$emit('click-left')
		},
		/**
		 * 右侧按钮点击事件
		 */
		onClickRight() {
			// this.$emit('clickRight')
			this.$emit('click-right')
		}
	}
}
